

































































































import { Vue, Component } from 'vue-property-decorator';
import { IconsData } from '@/types/views/components/icons.interface';
// import iconFontItems from '@/assets/jsons/iconFonts.json';
import { iconFontItems } from '@/assets/jsons/iconFonts.ts';
import IconFont from '@/components/IconFont.vue';
import IconFontGrid from '@/components/IconFontGrid.vue';

@Component({
  components: {
    IconFont,
    IconFontGrid
  }
})
export default class IconsView extends Vue {
  textItems!: object[];
  iconFontItems!: object;
  iconFontTab!: any;

  data(): IconsData {
    return {
      textItems: [
        {
          text:
            '提供了一套由D3III用户精心绘制的常用的以16/20/24网格绘制的线性/填充风格类型图标集合。详细包括：',
          lists: [
            '◇ 16网格线性类型图标 —— 577个',
            '◇ 16网格填充类型图标 —— 405个',
            '◇ 20网格线性类型图标 —— 577个',
            '◇ 20网格填充类型图标 —— 405个',
            '◇ 24网格线性类型图标 —— 577个',
            '◇ 24网格填充类型图标 —— 405个',
            '◇ 16网格线性莫比乌斯带风格英文字体图标 —— 46个'
          ],
          img: require('@/assets/images/portfolio/14/P0.png')
        },
        { text: '共合计2992（577 * 3 + 405 * 3 + 46）个不同图标。' }
      ],
      iconFontItems: {},
      iconFontTab: null
    };
  }

  // 生命周期钩子函数
  created() {
    this.iconFontItems = iconFontItems.data;
  }
}
